*{
    margin: 0;
    padding: 0;
    bottom: 0;
    list-style-type: none;
}
.all-bloc{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    background: rgb(120, 203, 236);
    justify-content: space-around;
    align-items: center;
    form {
        width: 90%;
        border-radius: 5em;
        background: rgb(163, 202, 235);
        height: 40vh;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        form{
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            height: 75%;
            align-items: start;
            input{
                border-radius: 2rem;
                text-align: center;
                width: 16em;
            }
        }
    }
    .user-list{
      margin-top: 25px;
        display: flex;
        flex-direction: column;
        background: rgb(37, 186, 240);
        width: 90%;
      
        justify-content: space-around;
        align-items: center;
    }
}
table {
    width: 100%;
    border-collapse: collapse;
  }
  
  th, td {
    padding: 12px;
    text-align: left;
    border-bottom: 1px solid #ddd;
  }
  
  th {
    background-color: #f4f4f4;
  }
  
  tr:hover {
    background-color: #f1f1f1;
  }